const DatFileResultSchema = z.object({
    success: z.boolean(),
    datContent: z.string().nullable(),
    fileName: z.string().nullable(),
    error: z.string().nullable(),
    errors: z.array(z.string()).nullable(),
    // Sales Totals
    totalExempt: z.number().nullable(),
    totalZeroRated: z.number().nullable(),
    totalTaxableSales: z.number().nullable(),
    totalOutputTax: z.number().nullable(),
    // Purchase Totals
    totalServices: z.number().nullable(),
    totalCapitalGoods: z.number().nullable(),
    totalOtherGoods: z.number().nullable(),
    totalInputTax: z.number().nullable(),
    processedData: z.any().nullable(),
});
type DatFileResult = z.infer<typeof DatFileResultSchema>;


function sanitizeAndValidateString(input: any, fieldName: string, maxLength: number, rowNumber: number, isRequired: boolean = false): { value: string, error: string | null } {
    let value = (input === null || input === undefined) ? '' : String(input).trim();
    
    if (!value) {
        if (isRequired) {
            return { value: '', error: `Row ${rowNumber}: ${fieldName} is missing.` };
        }
        return { value: '', error: null };
    }

    let processedString = value
        .toUpperCase()
        .replace(/&/g, 'AND')
        .replace(/Ã‘/g, 'N')
        .replace(/\s\s+/g, ' ')
        .trim()
        .replace(/[^A-Z0-9\s-]/g, '')
        .replace(/\s\s+/g, ' ')
        .trim();

    if (processedString.length > maxLength) {
        return { value: processedString, error: `Row ${rowNumber}: ${fieldName} must be ${maxLength} characters or less. It is currently ${processedString.length} characters.` };
    }

    return { value: processedString, error: null };
}

function sanitizeAndValidateNumber(input: any, fieldName: string, rowNumber: number): { value: string, error: string | null } {
    if (input === null || input === undefined || String(input).trim() === '') {
        return { value: '0', error: null };
    }
    
    const valueAsString = String(input).replace(/,/g, '');
    const num = parseFloat(valueAsString);

    if (isNaN(num)) {
        return { value: String(input), error: `Row ${rowNumber}: ${fieldName} contains an invalid number.` };
    }
    
    const roundedNum = Math.round(num * 100) / 100;
    
    if (roundedNum === 0) {
        return { value: '0', error: null };
    }

    return { value: roundedNum.toFixed(2), error: null };
}

function getFormattedLastDay(year: number, month: number): string {
    // Get the last day of the month. This correctly handles leap years.
    const day = new Date(year, month, 0).getDate();
    
    const date = new Date(year, month - 1, day);
    const mm = String(date.getMonth() + 1).padStart(2, '0');
    const dd = String(date.getDate()).padStart(2, '0');
    const yyyy = date.getFullYear();

    return `${mm}/${dd}/${yyyy}`;
}

const quoteIfNotEmpty = (value: string | undefined | null) => {
  const str = String(value || '').trim();
  return str ? `"${str}"` : '';
};

async function processExcelFile(file: File, sheetName: string): Promise<{ data: any[][], validationErrors: string[] }> {
    const bytes = await file.arrayBuffer();
    const workbook = xlsx.read(bytes, { type: 'array' });

    if (!workbook.SheetNames.includes(sheetName)) {
        return { data: [], validationErrors: [`Sheet "${sheetName}" not found in the uploaded file.`] };
    }

    const worksheet = workbook.Sheets[sheetName];
    const data: any[][] = xlsx.utils.sheet_to_json(worksheet, {
        header: 1,
        defval: '',
        raw: false,
    });

    if (data.length <= 1) {
        return { data: [], validationErrors: ["The sheet has no data to process."] };
    }

    return { data: data.slice(1), validationErrors: [] };
}


export async function convertExcelToDat(formData: FormData): Promise<DatFileResult> {
    const file = formData.get('file') as File | null;
    const reportType = formData.get('reportType') as string | null;
    const month = formData.get('month') as string | null;
    const year = formData.get('year') as string | null;
    const profileString = formData.get('profile') as string | null;

    const defaultErrorResult = {
        success: false, datContent: null, fileName: null, errors: null,
        totalExempt: null, totalZeroRated: null, totalTaxableSales: null, totalOutputTax: null,
        totalServices: null, totalCapitalGoods: null, totalOtherGoods: null, totalInputTax: null,
        processedData: null,
    };

    if (!file) return { ...defaultErrorResult, error: 'No file uploaded.' };
    if (!reportType) return { ...defaultErrorResult, error: 'Report type not specified.' };
    if (!month) return { ...defaultErrorResult, error: 'Month not specified.' };
    if (!year) return { ...defaultErrorResult, error: 'Year not specified.' };
    if (!profileString) return { ...defaultErrorResult, error: 'Profile not provided.' };

    const profile: TaxProfile = JSON.parse(profileString);
    const { tpTIN: tin } = profile;

    try {
        if (reportType === "Summary of Sales (SLS)") {
            const { data: dataRows, validationErrors: fileErrors } = await processExcelFile(file, "vat_sales");
            if (fileErrors.length > 0) return { ...defaultErrorResult, error: fileErrors[0] };

            const validationErrors: string[] = [];
            
            const processedData = dataRows.map((row, index) => {
                const originalRowNumber = index + 2;
                const processedRow = [...row];

                if (processedRow.length > 0 && String(processedRow[0]).trim()) {
                    const originalTin = String(processedRow[0]);
                    const sanitizedTin = originalTin.replace(/[^0-9]/g, '');

                    if (sanitizedTin.substring(0, 9) === tin) {
                        validationErrors.push(`Row ${originalRowNumber}: The TIN matches the selected profile's TIN. A company cannot make a sale to itself.`);
                    }

                    if (sanitizedTin.length < 9) {
                        validationErrors.push(`Row ${originalRowNumber}: TIN '${originalTin}' is too short. It must be at least 9 digits.`);
                    }
                    processedRow[0] = sanitizedTin.substring(0, 9);
                } else {
                     validationErrors.push(`Row ${originalRowNumber}: TIN is missing.`);
                }

                const nameFieldsInfo = [
                    { name: 'Registered Name', index: 1, maxLength: 150, required: true },
                    { name: 'Last Name', index: 2, maxLength: 30, required: false },
                    { name: 'First Name', index: 3, maxLength: 30, required: false },
                    { name: 'Middle Name', index: 4, maxLength: 30, required: false },
                    { name: 'Address 1', index: 5, maxLength: 30, required: false },
                    { name: 'Address 2', index: 6, maxLength: 30, required: false },
                ];
                nameFieldsInfo.forEach(field => {
                    const result = sanitizeAndValidateString(processedRow[field.index], field.name, field.maxLength, originalRowNumber, field.required);
                    if (result.error) validationErrors.push(result.error);
                    processedRow[field.index] = result.value;
                });
                
                const hasFirstName = String(processedRow[3]).trim().length > 0;
                const hasLastName = String(processedRow[2]).trim().length > 0;
                const hasMiddleName = String(processedRow[4]).trim().length > 0;
                if ((hasFirstName && !hasLastName) || (!hasFirstName && hasLastName)) validationErrors.push(`Row ${originalRowNumber}: First Name and Last Name must be provided together.`);
                if (hasMiddleName && (!hasFirstName || !hasLastName)) validationErrors.push(`Row ${originalRowNumber}: If Middle Name is provided, First Name and Last Name are also required.`);

                const numericFields = [
                    { name: 'Exempt Sales', index: 7 }, { name: 'Zero-Rated Sales', index: 8 },
                    { name: 'Taxable Sales', index: 9 }, { name: 'Output Tax', index: 10 },
                ];
                numericFields.forEach(field => {
                    const result = sanitizeAndValidateNumber(processedRow[field.index], field.name, originalRowNumber);
                    if (result.error) validationErrors.push(result.error);
                    processedRow[field.index] = result.value;
                });
                
                if (parseFloat(processedRow[7]) === 0 && parseFloat(processedRow[8]) === 0 && parseFloat(processedRow[9]) === 0) {
                    validationErrors.push(`Row ${originalRowNumber}: At least one sales amount (Exempt, Zero-Rated, or Taxable) must be greater than zero.`);
                }

                return processedRow;
            });

            if (validationErrors.length > 0) return { ...defaultErrorResult, success: false, error: "Validation failed.", errors: validationErrors };
            
            const lastDayDate = getFormattedLastDay(parseInt(year), parseInt(month));
            const address1 = [profile.subStreet, profile.street, profile.barangay].filter(Boolean).join(' ');
            const address2 = [profile.cityMunicipality, profile.province, profile.zipCode].filter(Boolean).join(' ');

            const detailRows = processedData.map(row => {
                const finalRow = [
                    'D', 'S',
                    quoteIfNotEmpty(row[0]), quoteIfNotEmpty(row[1]), quoteIfNotEmpty(row[2]),
                    quoteIfNotEmpty(row[3]), quoteIfNotEmpty(row[4]), quoteIfNotEmpty(row[5]),
                    quoteIfNotEmpty(row[6]),
                    row[7], row[8], row[9], row[10],
                    tin, lastDayDate,
                ];
                return finalRow.join(',');
            }).join('\n');
            
            const totalExempt = processedData.reduce((acc, row) => acc + parseFloat(row[7]), 0);
            const totalZeroRated = processedData.reduce((acc, row) => acc + parseFloat(row[8]), 0);
            const totalTaxableSales = processedData.reduce((acc, row) => acc + parseFloat(row[9]), 0);
            const totalOutputTax = processedData.reduce((acc, row) => acc + parseFloat(row[10]), 0);
            
            const headerRow = [
                'H', 'S', quoteIfNotEmpty(tin),
                quoteIfNotEmpty(profile.companyName), quoteIfNotEmpty(profile.lastName), quoteIfNotEmpty(profile.firstName),
                quoteIfNotEmpty(profile.middleName), quoteIfNotEmpty(profile.tradeName),
                quoteIfNotEmpty(address1), quoteIfNotEmpty(address2),
                totalExempt.toFixed(2), totalZeroRated.toFixed(2), totalTaxableSales.toFixed(2), totalOutputTax.toFixed(2),
                profile.rdoCode, lastDayDate, profile.monthSelect
            ].join(',');
            
            const datContent = `${headerRow}\n${detailRows}`;
            const datFileName = `${tin}S${month}${year}.DAT`;
                
            return { 
                ...defaultErrorResult, success: true, datContent, fileName: datFileName,
                totalExempt, totalZeroRated, totalTaxableSales, totalOutputTax
            };
        }
        return { ...defaultErrorResult, error: `Report type "${reportType}" is not yet supported.` };
    } catch (e) {
        const errorMessage = e instanceof Error ? e.message : 'An unknown error occurred during conversion.';
        console.error('[convertExcelToDat] CRITICAL ERROR:', e);
        return { ...defaultErrorResult, error: `Conversion failed: ${errorMessage}` };
    }
}

export async function validateExcelForPurchases(formData: FormData): Promise<DatFileResult> {
    const file = formData.get('file') as File;
    const profileString = formData.get('profile') as string;
    const profile: TaxProfile = JSON.parse(profileString);
    const { tpTIN: tin } = profile;

    const defaultErrorResult = {
        success: false, datContent: null, fileName: null, errors: null,
        totalExempt: null, totalZeroRated: null, totalTaxableSales: null, totalOutputTax: null,
        totalServices: null, totalCapitalGoods: null, totalOtherGoods: null, totalInputTax: null,
        processedData: null
    };

    const { data: dataRows, validationErrors: fileErrors } = await processExcelFile(file, "vat_purchases");
    if (fileErrors.length > 0) return { ...defaultErrorResult, error: fileErrors[0] };

    const validationErrors: string[] = [];
    const processedData = dataRows.map((row, index) => {
        const originalRowNumber = index + 2;
        const processedRow = [...row];

        if (String(processedRow[0] || '').trim()) {
            const originalTin = String(processedRow[0]);
            const sanitizedTin = originalTin.replace(/[^0-9]/g, '');
            if (sanitizedTin.substring(0, 9) === tin) validationErrors.push(`Row ${originalRowNumber}: The TIN matches the selected profile's TIN. A company cannot have a purchase from itself.`);
            if (sanitizedTin.length < 9) validationErrors.push(`Row ${originalRowNumber}: TIN '${originalTin}' is too short. It must be at least 9 digits.`);
            processedRow[0] = sanitizedTin.substring(0, 9);
        } else {
            validationErrors.push(`Row ${originalRowNumber}: TIN is missing.`);
        }

        const nameFieldsInfo = [
            { name: 'Registered Name', index: 1, maxLength: 150, required: true }, { name: 'Last Name', index: 2, maxLength: 30, required: false },
            { name: 'First Name', index: 3, maxLength: 30, required: false }, { name: 'Middle Name', index: 4, maxLength: 30, required: false },
            { name: 'Address 1', index: 5, maxLength: 30, required: false }, { name: 'Address 2', index: 6, maxLength: 30, required: false },
        ];
        nameFieldsInfo.forEach(field => {
            const result = sanitizeAndValidateString(processedRow[field.index], field.name, field.maxLength, originalRowNumber, field.required);
            if (result.error) validationErrors.push(result.error);
            processedRow[field.index] = result.value;
        });

        const hasFirstName = String(processedRow[3]).trim().length > 0;
        const hasLastName = String(processedRow[2]).trim().length > 0;
        const hasMiddleName = String(processedRow[4]).trim().length > 0;
        if ((hasFirstName && !hasLastName) || (!hasFirstName && hasLastName)) validationErrors.push(`Row ${originalRowNumber}: First Name and Last Name must be provided together.`);
        if (hasMiddleName && (!hasFirstName || !hasLastName)) validationErrors.push(`Row ${originalRowNumber}: If Middle Name is provided, First Name and Last Name are also required.`);

        const numericFields = [
            { name: 'Exempt Purchases', index: 7 }, { name: 'Zero-Rated Purchases', index: 8 },
            { name: 'Purchases of Services', index: 9 }, { name: 'Purchases of Capital Goods', index: 10 },
            { name: 'Purchases of Other Goods', index: 11 }, { name: 'Input Tax', index: 12 },
        ];
        numericFields.forEach(field => {
            const result = sanitizeAndValidateNumber(processedRow[field.index], field.name, originalRowNumber);
            if (result.error) validationErrors.push(result.error);
            processedRow[field.index] = result.value;
        });

        if (parseFloat(processedRow[7]) === 0 && parseFloat(processedRow[8]) === 0 && parseFloat(processedRow[9]) === 0 && parseFloat(processedRow[10]) === 0 && parseFloat(processedRow[11]) === 0) {
            validationErrors.push(`Row ${originalRowNumber}: At least one purchase amount must be greater than zero.`);
        }

        return processedRow;
    });

    if (validationErrors.length > 0) {
        return { ...defaultErrorResult, success: false, errors: validationErrors };
    }

    const totalInputTax = processedData.reduce((acc, row) => acc + parseFloat(row[12]), 0);

    return { ...defaultErrorResult, success: true, totalInputTax, processedData };
}

interface CreatePurchasesDatFileArgs {
    processedData: any[][];
    profile: TaxProfile;
    month: string;
    year: string;
    nonCreditableInputTax: number;
}

export async function createPurchasesDatFile({
    processedData,
    profile,
    month,
    year,
    nonCreditableInputTax
}: CreatePurchasesDatFileArgs): Promise<DatFileResult> {

    const defaultErrorResult = {
        success: false, datContent: null, fileName: null, errors: null,
        totalExempt: null, totalZeroRated: null, totalTaxableSales: null, totalOutputTax: null,
        totalServices: null, totalCapitalGoods: null, totalOtherGoods: null, totalInputTax: null,
        processedData: null
    };
    
    try {
        const { tpTIN: tin } = profile;
        const lastDayDate = getFormattedLastDay(parseInt(year), parseInt(month));
        const address1 = [profile.subStreet, profile.street, profile.barangay].filter(Boolean).join(' ');
        const address2 = [profile.cityMunicipality, profile.province, profile.zipCode].filter(Boolean).join(' ');

        const detailRows = processedData.map(row => {
            const finalRow = [
                'D', 'P',
                quoteIfNotEmpty(row[0]), quoteIfNotEmpty(row[1]), quoteIfNotEmpty(row[2]),
                quoteIfNotEmpty(row[3]), quoteIfNotEmpty(row[4]), quoteIfNotEmpty(row[5]),
                quoteIfNotEmpty(row[6]),
                row[7], row[8], row[9], row[10], row[11], row[12],
                tin, lastDayDate,
            ];
            return finalRow.join(',');
        }).join('\n');

        const totalExempt = processedData.reduce((acc, row) => acc + parseFloat(row[7]), 0);
        const totalZeroRated = processedData.reduce((acc, row) => acc + parseFloat(row[8]), 0);
        const totalServices = processedData.reduce((acc, row) => acc + parseFloat(row[9]), 0);
        const totalCapitalGoods = processedData.reduce((acc, row) => acc + parseFloat(row[10]), 0);
        const totalOtherGoods = processedData.reduce((acc, row) => acc + parseFloat(row[11]), 0);
        const totalInputTax = processedData.reduce((acc, row) => acc + parseFloat(row[12]), 0);
        const creditableInputTax = totalInputTax - nonCreditableInputTax;

        const headerRow = [
            'H', 'P', quoteIfNotEmpty(tin), quoteIfNotEmpty(profile.companyName),
            quoteIfNotEmpty(profile.lastName), quoteIfNotEmpty(profile.firstName), quoteIfNotEmpty(profile.middleName),
            quoteIfNotEmpty(profile.tradeName), quoteIfNotEmpty(address1), quoteIfNotEmpty(address2),
            totalExempt.toFixed(2), totalZeroRated.toFixed(2), totalServices.toFixed(2), totalCapitalGoods.toFixed(2), totalOtherGoods.toFixed(2), 
            totalInputTax.toFixed(2), creditableInputTax.toFixed(2), nonCreditableInputTax.toFixed(2),
            profile.rdoCode, lastDayDate, profile.monthSelect
        ].join(',');

        const datContent = `${headerRow}\n${detailRows}`;
        const datFileName = `${tin}P${month}${year}.DAT`;

        return { 
            ...defaultErrorResult,
            success: true, datContent, fileName: datFileName,
            totalExempt, totalZeroRated,
            totalServices, totalCapitalGoods, totalOtherGoods, totalInputTax
        };
    } catch (e) {
        const errorMessage = e instanceof Error ? e.message : 'An unknown error occurred during DAT file creation.';
        console.error('[createPurchasesDatFile] CRITICAL ERROR:', e);
        return { ...defaultErrorResult, error: `Creation failed: ${errorMessage}` };
    }
}
